<template>
    <el-row>
        <basic-header :items="['管理员管理','隐私管理']"></basic-header>
        <el-form :inline="true">
            <el-form-item>
                <el-input placeholder="请输入管理员姓名" v-model="value"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button circle type="primary" icon="el-icon-search" @click="search"></el-button>
            </el-form-item>
        </el-form>
        <el-table :data="tableData">
            <el-table-column label="id" prop="id"></el-table-column>
            <el-table-column label="用户名" prop="username"></el-table-column>
            <el-table-column label="操作">
                <template #default="o">
                    <el-button type="text" @click="reset(o.row.id)">重置密码</el-button>
                </template>
            </el-table-column>
        </el-table>
        <el-pagination
                background
                layout="prev,pager,next"
                :current-page.sync="page"
                :page-size.sync="pageSize"
                :total="total"
                @current-change="handleChange"
                style="margin-top: 30px"></el-pagination>
    </el-row>
</template>

<script>
    import BasicHeader from "@/components/BasicHeader"
    export default {
        name: "PrivateManage",
        data: () => ({
            tableData:[],
            //翻页的三个属性
            total:0,//总条数
            page:1,//当前页码
            pageSize:5, //每一页可以显示的数量
            value:""
        }),
        methods: {
            fetchData(){
                this.$axios.get(this.$api.MANAGER,{role:2,page:this.page,pageSize:this.pageSize,search:this.value}).then(res=>{
                    if(res.data.code===200){
                        this.tableData=res.data.data;
                        this.total=res.data.total;
                    }
                });
            },
            handleChange(){
                this.fetchData();
            },
            search(){
                this.page=1;
                this.fetchData();
            },
            reset(id){
                this.$axios.put(this.$api.MANAGER,{id,password:"123456"}).then(res=>{
                    if(res.data.code===200){
                        this.$message.success("更新成功");
                    }
                })
            }
        },
        components:{
            "basic-header":BasicHeader
        },
        mounted() {
            this.fetchData();
        }
    }
</script>

